.bicycle-selection {
  background-color: $grey-6;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 150px;
  padding-bottom: 150px;
  display: block;
  @media (max-width: $max-tablet) {
    padding-bottom: 70px;
    padding-top: 50px;
  }
}

.bicycle-selection__title {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  letter-spacing: -0.5%;
  margin-top: 0;
  color: $black-2;
  text-align: center;
  margin-bottom: 20px;
  @media (max-width: $max-tablet) {
    font-size: 24px;
    line-height: 41px;
    margin-bottom: 30px;
  }
}

.bicycle-selection__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, max-content));
  justify-content: center;
  padding-left: 80px;
  padding-right: 80px;
  grid-gap: 50px;
  margin: 0 auto;
  width: 100%;
  list-style: none;
  padding-top: 32px;
  box-sizing: border-box;
  position: relative;
  @media (max-width: $max-tablet) {
    padding-top: 0;
    display: flex;
    flex-wrap: wrap;
    grid-gap: unset;
    padding-left: 37px;
    padding-right: 37px;
    width: auto;
    justify-content: center;
  }
}

.bicycle-selection--mountain {
  padding-top: 39px;
  @media (max-width: $max-tablet) {
    padding-top: 0;
  }
}

.bicycle-selection__item {
  @media (max-width: $max-tablet) {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.bicycle-selection__item h3 {
  margin-top: 15px;
  margin-bottom: 0;
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
  letter-spacing: -0.5px;
  color: $black-1;
  @media (max-width: $max-tablet) {
    margin-top: 20px;
  }
}

.bicycle-selection__wrapper {
  position: relative;
  width: fit-content;
}

.bicycle-selection__wrapper img {
  display: block;
  border-radius: 10px;
  position: relative;
  z-index: 0;
  @media (max-width: $max-tablet) {
    height: 300px;
    width: 270px;
    object-fit: cover;
  }
}

.bicycle-selection__wrapper--city img {
  @media (max-width: $max-tablet) {
    object-position: 50% 60%;
  }
}

.bicycle-selection--mountain img {
  @media (max-width: $max-tablet) {
    object-position: 50% 40%;
  }
}

.bicycle-selection--kids img {
  @media (max-width: $max-tablet) {
    object-position: 50% bottom;
    margin-top: 50px;
  }
}

.bicycle-selection__wrapper--city::before {
  content: "";
  position: absolute;
  top: 267px;
  left: -57px;
  transform: translateY(-50%);
  border: 27px solid $basic-turquoise;
  border-radius: 50%;
  width: 180px;
  height: 180px;
  box-sizing: border-box;
  z-index: 0;
  @media (max-width: $max-tablet) {
    top: unset;
    bottom: -28px;
    left: -37px;
    -webkit-transform: none;
    transform: none;
    border-width: 18px;
    width: 127px;
    height: 127px;
  }
}

.bicycle-selection--mountain::before {
  content: "";
  position: absolute;
  z-index: 1;
  border: 8px solid $basic-turquoise;
  width: 47px;
  height: 47px;
  box-sizing: border-box;
  top: 21px;
  left: -6%;
  @media (max-width: $max-tablet) {
    display: none;
  }
}

.bicycle-selection--mountain::after {
  content: "";
  position: absolute;
  z-index: 1;
  border: 8px solid $basic-turquoise;
  width: 47px;
  height: 47px;
  box-sizing: border-box;
  right: -5.5%;
  bottom: -22px;
  @media (max-width: $max-tablet) {
    display: none;
  }
}

.bicycle-selection--kids::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: -34px;
  right: -38px;
  border: 17px solid $basic-turquoise;
  border-radius: 50%;
  width: 95px;
  height: 95px;
  box-sizing: border-box;
  @media (max-width: $max-tablet) {
    top: -20px;
    right: -25px;
    border-width: 13px;
    width: 68px;
    height: 68px;
  }
}
